<template lang="jade">
 .banner
  swiper(:options='swiperOptionz',ref="banner")
   swiper-slide.swiper-slide.bann_src1
   swiper-slide.swiper-slide.bann_src2
   swiper-slide.swiper-slide.bann_src3
</template>
<script>
	export default{
		data(){
			return{
				swiperOptionz:{
            autoplay:3000,
            loop: true,
            width:"100%"
				}
			}
		},
	}
</script>
<style lang="scss">
  @import "../../assets/css/main.scss";
.banner{
  width:rem2px(750);
  height:rem2px(300);
}
@for $i from  1 through 3{
  .bann_src#{$i}{
    width:rem2px(750) !important;
    height:rem2px(300) !important;
    background:url(../../assets/banner-1.jpg) no-repeat 0 0 /100%;
  }
}
</style>



