		margin-left:0.45rem;
		width:4.5rem;
		height:0.7rem;
		padding-left:0.3rem;
		border:1px solid #000;
		color:#898989;
	}
	.input_l{
	  float:left
	}
	.tip_r{
		float:right;
		margin-right:rem2px(30);
	}
</style>



