<template lang="jade">
.index
 bSlide
 .marqu
  .marr
   marquee(scrollamount=4)
    span 新平台游戏正式上线，试玩阶段请大家提出自己的宝贵意见或建议。谢谢！!
 .ishow
</template>
<script>
   //引入banner模块
  import bSlide from './banner/bannerSlide.vue';
	export default{
       components:{
       	 bSlide
       },
       methods:{
          swiperTo(mainValue,gameValue){
            if(mainValue==1){
              this.$store.commit("showToCenter",{main:mainValue,game:gameValue});
            }else{
              this.$store.commit("swiperTopage",mainValue);
            }
          }
       }
	}
</script>
<style lang="scss">
  @import "../assets/css/main.scss";
  .tipHead_1{
    @extend %m0a;
    @include Bg('showTip1.png',2.85rem,0.296875rem);
    background-size:2.85rem,0.296875rem;
  }
  .tipHead{
    @extend %m0a;
    @include Bg('showTip2.png',3.5rem,0.3125rem);
    background-size:3.5rem 0.3125rem;
  }
  .logo{
  	 @extend %fl;
     @include Bg("logo.png",rem2px(105),rem2px(36))
     margin-left:rem2px(30);
     @extend %bsc;
  }
  .marqu{
  	 @include Bg('marBg.jpg',100%,rem2px(47));
     @extend %m0a;
     display:flex;
     align-items:center;
     justify-content:flex-end;
     width:rem2px(750);
     height:rem2px(47);
  	 background-size:100% ;
  	 box-sizing:border-box;
  	 color:#ffff;
  	 @extend %font16;
  	 .marr{
       width:75%;
       @extend %fl;
       display:flex;
       align-items:center;
	    >marquee{
	    	width:rem2px(500);
        vertical-align:middle;
        span{
          @extend %scale0.7;
        };
	    }
  	 }
  }
  .ishow{
    @include Bg('index_bg.jpg',rem2px(750),rem2px(790));
  	box-sizing:border-box;
    background-size:rem2px(750) rem2px(790);
  	.head{
  		@include Bg("showHead.png",3.125rem,0.78rem)
  		margin: 0 auto 0;
  		@extend %bsc;
  	}
  	.center{
  		display:flex;
      display:-webkit-flex;
  		height:1.4rem;
  		margin:0.225rem 0 0 0.55rem ;
  		p{
  			@extend %fl;
        @extend %bsc;
  		}
      @for $i from 1 through 3{
        p:nth-child(#{$i}){
          @if($i==1){
             @include Bg("sgame.png",2.82rem,1.28rem)
             margin-top:0.12rem;
          }@else if($i==2){
              @include Bg("sdealer.png",3.01rem,1.56rem)
              margin:0 0.158rem;
          }@else{
            margin-top:0.10rem;
            @include Bg("slottery.png",2.82rem,1.28rem)
          }
        }
      }
  	}
  	.bottom{
  		color:#4b4b4b;
  		margin-top:0.50rem;
  		width:3rem;
  		font-weight:500;
  		@extend %font24;
  		@extend %m0a;
  	}
  }
  .ipromotion{
  	height:2.265rem;
  	background-color:#f7f7f7;
  	padding-top:0.265rem;
    display:-webkit-flex;
    justify-content:center;
  	@extend %bz;
  	p{
  		@extend %fl;
  		@extend %bsc;
  	}
    @for $i from 1 through 3{
      p:nth-child(#{$i}){
        @include Bg('iprom'+$i+".png",2.93rem,1.78rem);
        border-radius:0.1rem;
        background-size:100%;
        @if($i==1){
        }@else if($i==2){
          margin:0 0.1875rem 0;
          float: left;
        }
      }
    }
  }
  .line{
  	background-color:#4b4b4b;
  	height:0.033rem;
  	@extend %m0a;
  }
  .iseu {
  		height:4.14rem;
  	 .head{
  	 	@extend %font24;
  	 }
  	 .up{
  	 	width:8rem;
  	 	height:0.65rem;
  	 	@extend %m0a;
  	 	margin-top:0.56rem;
  	 	p{
  	 		@extend %bsc;
  	 	}
  	 	p:first-child{
  	 		@include Bg("scu_1.png",3.10rem,0.578rem)
  	 		@extend %fl;
  	 	}
  	 	p:last-child{
  	 		@include Bg("scu_2.png",3.375rem,0.60rem)
  	 		@extend %fr;
  	 	}
  	 }
  	 .down{
  	 	width:8.4rem;
  	 	@extend %m0a;
  	 	margin-top:0.6rem;
  	 	p{
  	 		@extend %bsc;
  	 	}
  	 	p:first-child{
  	 		@include Bg("scu_3.png",3.20rem,0.6rem)
  	 		@extend %fl;
  	 		margin-left:0.22rem;
  	 	}
  	 	p:last-child{
  	 		@include Bg("scu_4.png",3.578rem,0.53rem)
  	 		@extend %fr;
  	 	}
  	 }
  }
</style>



